import {Component} from "react"
import React from "react"

class Historique extends Component {
    render() {
        return (
            <div>
                <h2>Historique</h2>
                <h3>Il était une fois</h3>
                <h3>1963 - 1977</h3>
                <p>En décembre 1963, des parents de personnes handicapées fondent le Village n°1. L’initiative répond à une inquiétude grandissante : « que deviendront nos enfants quand nous ne serons plus là ? ». Dans les années '60, les alternatives au home ou à la famille sont rares. Au départ d’une grande ferme en Brabant wallon, le Village se développe autour de deux axes principaux : des ateliers de travail adapté et des maisons d’accueil pour personnes handicapées. Les initiateurs du Village n°1 se sont inspirés du « Botton Village », petit village anglais où travaillent depuis 1955, des personnes atteintes de handicap et des accompagnateurs.
                En Belgique, la législation évolue et ouvre de nouveaux horizons. La loi du 16 avril 1963 instaure le Fonds national de reclassement social des handicapés. Le Fonds prévoit un système d’emploi pour les personnes handicapées via des ateliers protégés. En 1967, un arrêté royal institue le Fonds 81. Les instituts médico-pédagogiques (centres de jour et homes d'hébergement)seront désormais reconnus et subsidiés.
                En mai 1977, le Village compte déjà cinq maisons d’hébergement adaptées aux handicapés : la Dansette, la Grange des champs, la Flohaye et la Graignette. Parallèlement, les ateliers se développent dans de nombreux domaines : emballage, étiquetage, stockage.</p>
                <h3>1975 - 1997</h3>
                <p>Pour offrir des activités répondant aux aptitudes de chacun, le Village crée le centre de jour en mai 1980. Grâce à l’achat de la Ferté – construction voisine de la ferme initiale - en 1978, les locaux ne manquent pas. Le centre propose une alternative aux ateliers protégés à travers des activités diverses : sérigraphie, boulangerie, menuiserie, fabrication textile, production alimentaire.
                Dorénavant, le Village se décline selon trois départements : l’hébergement, le travail et les activités de jour.
                Trois nouvelles maisons s’ajoutent au Village dans les années ’80. Les services d’hébergement proposent ainsi un panel de possibilités de plus en plus complet. Une boutique - la Salangane, créée en 1984 – vend les produits fabriqués par les personnes handicapées au sein du centre de jour.
                Sur les sites d’Ophain et Noucelles 1 à Wauthier-Braine, le Village accueille progressivement de plus en plus de travailleurs résidents hors du Village.
                La Belgique se fédéralise dans les années ’70. Le secteur du handicap dépend à présent de la région Wallonne qui crée le décret du 6 avril 1995. Ce décret donne naissance à l’Agence Wallonne pour l'Intégration des Personnes Handicapées et transforme les ateliers protégés en entreprises de travail adapté.</p>
                <h3>1998 - 2008</h3>
                <p>Les affaires judiciaires éclatent à la fin des années ‘90. Le Village remanie alors son organisation et son fonctionnement. Les activités industrielles, projets pédagogiques et stratégies d’entreprises sont totalement repensés.
                Dans les années 2000, les installations industrielles du Village connaissent un essor considérable. L’achat de Noucelles 2 à Wauthier-Braine en 2005 permet un développement important des activités. En dix ans, le personnel passe de 400 à 1000 travailleurs dans divers domaines : conditionnement, logistique documentaire, entretien d’espaces verts.
                En 2005, pour répondre à des besoins sociaux en matière d’emploi, le Village crée deux entreprises d’insertion. Il s’ouvre ainsi à d’autres types de publics précarisés. </p>
                <h3>2008 à aujourd'hui</h3>
                <p>A la fin de l’année 2008, comme toute entreprise le Village doit faire face à la crise. La résistance s’organise. Pour sauver ses travailleurs et sa santé économique, le Village réagit et acquiert de nouveaux clients et contrats. Afin de rester aussi compétitif que possible, une nouvelle organisation est mise en place. L’entreprise est restructurée par pôles d’activités et de support, et les fonctions d’encadrement dynamisées.
                Initialement destiné aux personnes en situation de handicap mental léger, le Village ouvre aujourd’hui ses portes à tout type de handicap. Les services résidentiels, secondés par les services transversaux du Village, accompagnent chaque usager dans son projet de vie spécifique. La pédagogie originelle axée sur l’occupationnel subit une mutation profonde favorisant l’accès à l’autonomie de chacun.
                Le Village n°1 est en développement constant. Il favorise une évolution permanente des solutions proposées aux usagers, aux clients et aux travailleurs. A travers une structure dynamique, le Village garantit ainsi des services diversifiés, adaptés, personnalisés et efficaces.</p>
                <h3>2014 - 2016</h3>
                <p>Le Conseil d’Administration du Village n°1 a décidé de séparer les activités de l’Entreprise de Travail Adapté de celles des services résidentiels et de l’accueil de jour. En effet, si le Village n°1 accompagne toujours des personnes porteuses d’un handicap, il est devenu au fil des ans un groupe d’ASBL et d’entreprises sociales dont le champ d’activités s’est considérablement élargi: secteur de l’insertion, bénéficiaires de CPAS, personnes dépendantes physiquement mais aptes à travailler, … Le contexte évolue donc et les objectifs, les bénéficiaires, le personnel, les ressources ainsi que les dépenses sont devenus différents et obligent à une constante remise en question. Voilà pourquoi les instances du Village n°1 ont décidés de séparer les deux secteurs d’activités.</p>
                <h4>Décembre 2015, un nouveau conseil d’Administration est constitué pour le secteur Entreprises. Début 2016, le processus de scission entre les deux entités est en cours de finalisation.</h4>
            </div>
        )
    }
}

export default Historique;



